import { getErrorMessage } from "@/errorHandler";
import api from "@/utils/api";
import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { initialPostState, Post } from "../types/post";
const initialState: typeof initialPostState = initialPostState;


export const createPost = createAsyncThunk(
	'profile/createPost',
	async (post: Partial<Post>, { rejectWithValue }) => {
		try {
			const response = await api.post('/profiles/posts',post);
			return response.data;
		} catch (error: unknown) {
			return rejectWithValue(getErrorMessage(error));
		}
	}
);


export const postSlice = createSlice({
	name: 'post',
	initialState,
	reducers: {
		clearPost: (state) => {
			state.posts = [];
			state.error = null;
		},

		// Clear error
		clearError: (state) => {
			state.error = null;
		},

		// Clear posts list
		clearPosts: (state) => {
			state.posts = [];
		},
	},
    extraReducers: (builder)=>{
        builder
        .addCase(createPost.pending,(state)=>{
            state.isLoading = true;
    }).addCase(createPost.fulfilled,(state,action)=>{
        state.isLoading = false;
        state.post = action.payload;
        state.posts.unshift(action.payload);
    }).addCase(createPost.rejected,(state,action)=>{
        state.isLoading = false;
        state.error = action.payload as string;
    })
    }
});
export const { clearPost, clearError, clearPosts } = postSlice.actions;
export default postSlice.reducer;